import React, {useState, useEffect} from 'react'
import { PanelHeader, Panel, Card, Input, Checkbox, Button, Group, Header, Spinner,
  Snackbar, Avatar} from '@vkontakte/vkui'
import {Icon16ErrorCircleOutline, Icon16Done} from '@vkontakte/icons'

import UserUrl from './UserUrl'
import bridge from '@vkontakte/vk-bridge'

import './Home.css'

const Home = ({id, fetchedUser, setModal, setPopout}) => {
    const [userLinks, setUserLinks] = useState(<Spinner size="medium" style={{ margin: '20px 0' }} />)
    const [snackbar, setSnackbar] = useState(null)
    const [upload, setUpload] = useState(false)
    const [blocked, setBlocked] = useState(0)

    const restart = () => {
      setUpload(!upload)
    }

    const showBanner = () => {
      let x = Math.round(Math.random() * 3)
      if (x = 2) {
        bridge.sendPromise('VKWebAppShowNativeAds', { ad_format: 'interstitial' })
      }
    }

    const newLink = async () => {
      try {
      const token = await bridge.send('VKWebAppGetAuthToken', {
        app_id: 7790406,
        scope: '',
      })

      let item = document.getElementById('home-input')
      console.log(blocked);
       const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
            method: 'utils.getShortLink',
            request_id: '324nnefj',
    
            params: {
              v: '5.95',
              url: item.value,
              private: blocked,
              access_token: token.access_token,
            },
        })
        setSnackbar(
          <Snackbar
              onClose={() => setSnackbar(null)}
              before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
            >
              Ссылка сохранена!
            </Snackbar>
            )
      restart()
      showBanner()
      } catch (e) {
        setSnackbar(<Snackbar
          onClose={() => setSnackbar(null)}
          before={<Avatar size={24} style={{ background: 'var(--destructive)' }}>
              <Icon16ErrorCircleOutline fill="#fff" width={14} height={14} />
              </Avatar>}
        >
            Ошибка! Попробуйте позже!
        </Snackbar>)
      }
  }

    useEffect(() => {
        const start = async () => {
            const token = await bridge.send('VKWebAppGetAuthToken', {
                app_id: 7790406,
                scope: '',
              })
        
            const photos = await bridge.sendPromise('VKWebAppCallAPIMethod', {
            method: 'utils.getLastShortenedLinks',
            request_id: '324nnefj',
    
            params: {
              v: '5.95',
              count: 50,
              offset: 0,
              access_token: token.access_token,
            },
            })
            setUserLinks(<UserUrl 
              links={photos.response} 
              setModal={setModal} 
              setSnackbar={setSnackbar}
              setPopout={setPopout}
              restart={restart}
              />)
        }
          start()
    }, [upload])

    return (
    <Panel id={id}>
        <PanelHeader>Сокращение cсылок</PanelHeader>
        <Card mode="shadow" style={{margin: 5, padding: 5, fontSize: 14, textAlign: 'center'}}>
            <div >
На этой странице вы можете сделать из длинной и сложной ссылки простую.
Такие ссылки удобнее использовать в ваших записях и сообщениях.
            </div>
        </Card >
        <Input style={{margin: 5}} type="text" placeholder="Ссылка, которую вы хотите сократить" id='home-input'/>
        <Checkbox id='home-checkbox' onClick={() => {
          let it = 1
          if (blocked === 1) {
            it = 0
          }
          setBlocked(it)
        }}>Статистика ссылки видна только вам</Checkbox>
        <Button size="m" style={{margin: 5}} onClick={newLink}>Сократить</Button>
        <Group>
        <Header mode="secondary">Недавно сокращённые ссылки</Header>
        {userLinks}
        </Group>
        {snackbar}
    </Panel>
    )
}

export default Home